html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Mono", monospace;
  min-height: 100vh;
  background-color: #c5e4e7;
}

h1,
h2,
h3 {
  margin-top: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 34.375rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container {
    max-width: 56.25rem;
    padding: 0 1rem;
  }
}

.header {
  text-align: center;
  margin: 2.5rem auto 2rem;
}
.header__logo {
  object-fit: contain;
}
@media (min-width: 64em) {
  .header {
    margin-bottom: 4rem auto 3rem;
  }
}

.calc__container {
  background-color: white;
  padding: 1.5rem;
  border-radius: 1rem 1rem 0 0;
}

.form label,
.form legend {
  display: inline-block;
  color: #5e7a7d;
  font-weight: 700;
  margin: 0 0 0.4rem;
  font-size: 1.125rem;
}
.form input:-webkit-autofill,
.form input:-webkit-autofill:hover,
.form input:-webkit-autofill:focus,
.form textarea:-webkit-autofill,
.form textarea:-webkit-autofill:hover,
.form textarea:-webkit-autofill:focus,
.form select:-webkit-autofill,
.form select:-webkit-autofill:hover,
.form select:-webkit-autofill:focus {
  border: none;
  -webkit-text-fill-color: none;
  background-color: #f4fafa;
  -webkit-text-fill-color: #00494d !important;
  transition: background-color 5000s ease-in-out 0s;
}

.input-group {
  margin: 0;
}
.input-group span {
  display: block;
  position: relative;
}
.input-group input {
  text-align: right;
  width: 100%;
  padding: 0.4rem 0.8rem;
  color: #00494d;
  font-size: 1.125rem;
  font-weight: 700;
  background-color: #f4fafa;
  border: none;
  outline: none;
}
.input-group img {
  position: absolute;
  top: 50%;
  padding: 0rem 0.8rem;
  transform: translateY(-50%);
}

.tip-percent {
  border: none;
  padding: 0;
  margin: 1.5rem 0;
}